<template>
    <b-row class="justify-content-center">
        <b-col cols="8">
            <h2 class="text-center">Info about Primary Passenger</h2>
            <ValidationProvider name="Full Name" rules="required|alpha_spaces" v-slot="{ errors }">
                <b-form-group 
                    label="Full Name" 
                    label-cols-sm="4"
                    label-cols-lg="3">
                        <b-form-input  
                            placeholder="Enter Full Name" 
                            :state="errors.length == 0 ? null : false" 
                            v-model="form.name"
                            name="name"
                        >
                        </b-form-input>
                        <b-form-invalid-feedback :state="errors.length == 0">{{ errors[0] }}</b-form-invalid-feedback>
                </b-form-group>
            </ValidationProvider>
            <ValidationProvider name="Age" rules="required|integer|max:3" v-slot="{ errors }">
                <b-form-group 
                    label="Age"
                    label-cols-sm="4"
                    label-cols-lg="3">
                        <b-form-input 
                            placeholder="Enter Age" 
                            :state="errors.length == 0 ? null : false" 
                            v-model="form.age"
                            name="age">
                        </b-form-input>
                        <b-form-invalid-feedback :state="errors.length == 0">{{ errors[0] }}</b-form-invalid-feedback>
                </b-form-group>
            </ValidationProvider>
            <ValidationProvider name="Email" rules="required|email" v-slot="{ errors }">
                <b-form-group 
                    label="Email"
                    label-cols-sm="4"
                    label-cols-lg="3">
                        <b-form-input  
                            placeholder="Enter Email"   
                            :state="errors.length == 0 ? null : false" 
                            v-model="form.email"
                            name="email">
                            </b-form-input>
                        <b-form-invalid-feedback :state="errors.length == 0">{{ errors[0] }}</b-form-invalid-feedback>
                </b-form-group>
            </ValidationProvider>
            <ValidationProvider name="Countries" rules="required" v-slot="{ errors }">
                <b-form-group 
                    label="Country"
                    label-cols-sm="4"
                    label-cols-lg="3">
                        <b-form-select 
                            :options="countries"  
                            :state="errors.length == 0 ? null : false" 
                            v-model="address.country"
                            placeholder="Enter Country">
                        </b-form-select>
                        <b-form-invalid-feedback :state="errors.length == 0">{{ errors[0] }}</b-form-invalid-feedback>
                </b-form-group>
            </ValidationProvider>
            <ValidationProvider name="Street" rules="required" v-slot="{ errors }">
                <b-form-group 
                    label="Street"
                    label-cols-sm="4"
                    label-cols-lg="3">
                        <b-form-input 
                            placeholder="Enter Street"  
                            :state="errors.length == 0 ? null : false" 
                            v-model="address.street"
                            name="street">
                            </b-form-input>
                        <b-form-invalid-feedback :state="errors.length == 0">{{ errors[0] }}</b-form-invalid-feedback>
                </b-form-group>
            </ValidationProvider>
            <ValidationProvider name="Index/Zip" rules="required|digits:5" v-slot="{ errors }">
                <b-form-group 
                    label="Index/Zip"
                    label-cols-sm="4"
                    label-cols-lg="3">
                        <b-form-input 
                            placeholder="Enter Index/Zip"  
                            :state="errors.length == 0 ? null : false" 
                            v-model="address.zip"
                            name="zip">
                            </b-form-input>
                        <b-form-invalid-feedback :state="errors.length == 0">{{ errors[0] }}</b-form-invalid-feedback>
                </b-form-group>
            </ValidationProvider>
            <ValidationProvider name="Phone Number" rules="required|digits:13" v-slot="{ errors }">
                <b-form-group 
                    label="Phone Number"
                    label-cols-sm="4"
                    label-cols-lg="3">
                        <b-form-input 
                            placeholder="Enter Phone Number"  
                            :state="errors.length == 0 ? null : false" 
                            v-model="form.phone"
                            name="phone">
                            </b-form-input>
                        <b-form-invalid-feedback :state="errors.length == 0">{{ errors[0] }}</b-form-invalid-feedback>
                </b-form-group>
            </ValidationProvider>
        </b-col>
    </b-row>
</template>
<script>
import {COUNTRIES} from '../../../helpers/counries'
export default {
    props:{
        form:{
            type: Object,
            required: true
        },
        address:{
            type: Object,
            required: true
        }
    },
    computed:{
        countries(){
            return COUNTRIES.map(c => ({ value: c.name, text: c.name }))
        }
    }
}
</script>