<template>
    <div>
        <b-row class="justify-content-center">
            <b-col cols="12" class="text-right">
                <b-button  type="button" variant="primary"  @click="addPassenger">Add Passenger</b-button>
            </b-col>
            <b-col cols="8">
                <h2 class="text-center">Info about Secondary Passenger</h2>
                <div v-for="(field ,index) in form" :key="index" class="secondaryPassenger">
                    <ValidationProvider name="Full Name secondary passenger" rules="required|alpha_spaces" v-slot="{ errors }">
                        <b-form-group 
                            label="Full Name" 
                            label-cols-sm="4"
                            label-cols-lg="3">
                                <b-form-input  
                                    placeholder="Enter Full Name" 
                                    :state="errors.length == 0 ? null : false" 
                                    v-model="field.name"
                                    name="seconrady_name"
                                >
                                </b-form-input>
                                <b-form-invalid-feedback :state="errors.length == 0">{{ errors[0] }}</b-form-invalid-feedback>
                        </b-form-group>
                    </ValidationProvider>
                    <ValidationProvider name="Age secondary passenger" rules="required|integer|max:3" v-slot="{ errors }">
                        <b-form-group 
                            label="Age"
                            label-cols-sm="4"
                            label-cols-lg="3">
                                <b-form-input 
                                    placeholder="Enter Age" 
                                    :state="errors.length == 0 ? null : false" 
                                    v-model="field.age"
                                    name="seconrady_age">
                                </b-form-input>
                                <b-form-invalid-feedback :state="errors.length == 0">{{ errors[0] }}</b-form-invalid-feedback>
                        </b-form-group>
                    </ValidationProvider>
                    <b-button 
                        type="button" 
                        @click="removePassenger(index)" 
                        v-if="form.length > 1"
                        variant="danger"
                        >
                        Delete
                    </b-button>
                </div>
            </b-col>
        </b-row>
    </div>
</template>
<script>
export default {
    props:{
        form:{
            type: Array,
            required: true
        }
    },
    methods:{
        addPassenger(){
            this.form.push({
                name:null,
                age:null
            })
        },
        removePassenger(index){
            this.form.splice(index,1)
        }
    },
}
</script>
<style scoped>
.secondaryPassenger{
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.15);
    margin:10px 0px;
    padding: 5px;
    text-align: right
}
</style>