<template>
  <ValidationObserver v-slot="{ passed }"  ref="form" tag="form" class="stepForm">
        <keep-alive>
            <PrimaryPassenger   v-if="currentStep === 1"  :form="form" :address="address"  ></PrimaryPassenger>
            <SecondaryPassenger v-if="currentStep === 2"  :form="form.secondaryForm"  ></SecondaryPassenger>
        </keep-alive>
        <b-row class="justify-content-center mt-3">
            <b-button-group>
                <b-button  
                    size="lg"
                    type="button" 
                    variant="light"
                    class="mx-1"
                    v-if="currentStep == 2" 
                    @click="goStep(currentStep - 1)" 
                >
                    Back 
                </b-button >
                <b-button 
                    size="lg"
                    type="button"
                    class="mx-1"
                    variant="primary"
                    @click="goStep(currentStep + 1)" 
                    v-if="currentStep == 1" 
                >
                   Next
                </b-button >
                <b-button 
                    size="lg"
                    type="button"
                    class="mx-1"
                    variant="primary"
                    @click="submit" 
                    v-if="currentStep == 2"
                >
                   Submit
                </b-button >
            </b-button-group>
        </b-row>
  </ValidationObserver>
</template>

<script>
import PrimaryPassenger from './Steps/PrimaryPassenger'
import SecondaryPassenger from './Steps/SecondaryPassenger'
export default {
  components:{
    PrimaryPassenger,
    SecondaryPassenger
  },
  computed:{
    setAddress(){
      return `${this.address.country} ${this.address.street} ${this.address.zip}`
    },
  },
  data(){
    return{
      address:{
        country: null,
        street: null,
        zip: null,
      },
      form:{
        name: null,
        age: null,
        email: null,
        phone: null,
        address: null,
        secondaryForm:[
          {
            name:null,
            age:null
          }
        ]
      },
      currentStep: 1,
    }
  },
  methods: {
    goStep(step){
        this.currentStep = step;
    },
    submit(){
      this.$refs.form.validate().then(success => {
        if (!success) {
          return;
        }
        this.form.address = this.setAddress
        console.log(this.form);
        this.$swal({type: 'success',title: 'Successfully sent !'})
      });
    }
  }
};
</script>
<style scoped>
.stepForm{
    padding: 10px;
    box-shadow: 0 0 1px rgba(34, 25, 25, 0.4);
}
</style>